import styled from 'styled-components'

interface Props {
    collection: {

    };
    onClick: (event: React.MouseEvent) => void;
    selected: boolean;
}

interface IProps {
    onClick: (event: React.MouseEvent) => void;
    selected: boolean;

};

const Collection: React.FC<Props> = ({collection, onClick, selected}) => {
        console.log(selected)
    return (
        <CollectionContainer onClick={(event:React.MouseEvent) => onClick(event)} selected={selected}>
        </CollectionContainer>
    );

}


const CollectionContainer = styled.li<IProps>`
    list-style: none;
    width: 100px;
    height: 100px;
    background-color: black;
    margin: 40px;
    display: ${props => props.selected ? 'inline-block' : 'none'};
`

export default Collection